import React, { Component } from "react";
import "./navbar.css";
import { Link } from "react-router-dom";

class NavBar extends Component {
  state = { burgerClass: "burger", translateMenu: "" };

  componentDidMount = () => {
    this.state.translateMenu = window.innerWidth < 769 ? "100%" : "0%";
    this.setState(this.state);
    console.log(window.innerWidth);
    window.addEventListener("resize", this.handleResize);
  };

  handleResize = () => {
    this.state.translateMenu = window.innerWidth < 769 ? "100%" : "0%";
    this.setState(this.state);
  };

  handleClickBurger = () => {
    this.state.burgerClass =
      this.state.burgerClass === "burger" ? "burger cross" : "burger";
    if (window.innerWidth < 769) {
      this.state.translateMenu =
        this.state.translateMenu === "100%" ? "0%" : "100%";
    }

    this.setState(this.state);
  };
  render() {
    return (
      <div className="navbar-container">
        <div
          className={this.state.burgerClass}
          onClick={this.handleClickBurger}
        >
          <div className="Line1"></div>
          <div className="Line2"></div>
          <div className="Line3"></div>
        </div>
        <Link to="/">
          <div className="logo"></div>
        </Link>
        <div className="nav-elements">
          <ul
            style={{ transform: `translateX(${this.state.translateMenu})` }}
            className="link-list"
          >
            <Link to="/">
              <li onClick={this.handleClickBurger} className="links">
                Home
              </li>
            </Link>
            <Link to="/about">
              <li onClick={this.handleClickBurger} className="links">
                About
              </li>
            </Link>
            <li onClick={this.handleClickBurger} className="links">
              NewsLetter
            </li>
            <Link to="/contact">
              <li onClick={this.handleClickBurger} className="links">
                Contact
              </li>
            </Link>
          </ul>
        </div>
      </div>
    );
  }
}

export default NavBar;
