import React, { Component } from "react";
import "./car-image-scroller.css";
import ArrowScroll from "../utility/arrow-scroll-indicator-svg";

class ScrollCards extends Component {
  imgElement = [];

  scrollVal = 0;

  componentWillReceiveProps(newProps) {
    // console.log(newProps);
    this.state.carImages = newProps.carDetails;
    this.setState(this.state);
  }

  componentDidMount = () => {
    console.log("helo");
    console.log(this.props);
    this.state.carImages = this.props.carDetails;
    this.props.getScrollCardRef(this.scrollWindow);
    this.setState(this.state);
    this.scrollWindow.addEventListener("scroll", this.hideAnimation);
  };

  hideAnimation = () => {
    if (this.scrollWindow.scrollTop < 51) {
      console.log(this.scrollWindow.scrollTop);
      this.state.display = "block";
    } else {
      this.state.display = "none";
    }
    this.setState(this.state);
  };

  handleScroll = () => {
    // getting the value of px between

    let val =
      (this.scrollWindow.scrollHeight - this.scrollWindow.scrollTop) /
      (1 * this.scrollWindow.clientHeight);
    let final_val = this.state.carImages.length - Math.round(val);
    if (this.state.image_number != final_val) {
      this.state.image_number = final_val;
      // console.log(this.state.image_number);
      this.props.scrollIndicator(final_val);
      this.setState(this.state);
    }
  };

  state = {
    image_number: 0,
    carImages: [],
    scrollVal: 0,
    display: "block",
  };
  //   style={{ transform: `translateX(${this.state.translate}%)` }}
  render() {
    return (
      // <div className="scroll-car-wrapper">
      <div
        className="scroll-card"
        ref={(el) => {
          this.scrollWindow = el;
        }}
        onScroll={this.handleScroll}
      >
        <div
          style={{ display: this.state.display }}
          className="arrow-scroll-anime"
        ></div>

        {this.state.carImages.map((image) => (
          <div
            onClick={() => this.props.popUp(image.image_number)}
            style={{ backgroundImage: `url(${image.url})` }}
            key={image.image_number}
            ref={(el) => {
              this.imgElement.push(el);
            }}
            id={this.state.image_number === image.image_number ? "active" : ""}
            className={"scroll-image"}
          ></div>
        ))}
      </div>
    );
  }
}

export default ScrollCards;
