import React, { Component } from "react";
import ScrollCards from "./car-image-scroller";

import axios from "axios";
import "./carDetails.css";

import Contact from "./contact";
// import TechnicalDetails from "../utility/technical-details";

class CarDetails extends Component {
  scrollIndicator;
  carLargeImagesContainer;
  state = {
    pictures: [],
    closePopUp: true,
    carDetails: {},
    detailType: "Engine",
    allDetails: {},
    engineDetails1: {},
    engineDetails2: {},
    conditionDetails: {},
    featuresListOne: [],
    featuresListTwo: [],
    featuresListThree: [],
    image_number: 0,
    translate: 100,
    scrollCardRef: "",
  };

  componentDidMount = () => {
    window.scrollTo(0, 0);
    let href = window.location.href.split("=");
    let id = href[1];

    axios
      .get("https://wallyn-messenger.herokuapp.com/carDetails?id=" + id)
      .then((res) => {
        let result = res.data.result;
        this.state.allDetails = result;
        let picList = [];
        console.log(result);
        let c = 0;
        for (let x in result["pictures"]) {
          picList.push({ image_number: c, url: result["pictures"][x] });
          c += 1;
        }
        let obj = {};
        obj.km = result["km"] || "NA";
        obj.type = result["type"];
        obj.gearbox =
          result["gearbox"] === "A"
            ? "Automatic"
            : "Gearbox(" + result["gearbox"] + ")" || "NA";
        obj.feul = result["carbDescription"] || "NA";
        obj.pollution = result["euro"] || "NA";
        obj.name = result["make"] + " " + result["model"];
        obj.price = result["price"] || "NA";
        obj.date =
          (result["month"].toString().length === 1
            ? "0" + result["month"].toString()
            : result["month"].toString()) +
            "/" +
            result["year"].toString() || "NA";
        obj.tax = result["vat"] > 0 ? "+VAT" : "MARGE" || "NA";
        this.state.carDetails = obj;
        this.state.pictures = picList;
        console.log(this.state);
        this.setState(this.state);
        this.getEngineDetails();
        this.getConditionDetails();
        this.getFeatures();
      })
      .catch((err) => {
        console.log(err);
      });
  };

  getFeatures = () => {
    let { allDetails } = this.state;
    let l = [];

    for (let i in allDetails["optionsCodes"]) {
      l.push(allDetails["optionsCodes"][i]);
    }
    let size = l.length;
    console.log(size);
    let listSize = Math.floor(size / 3);
    let sizeList = [];
    for (let i = 0; i < 3; i++) {
      sizeList[i] = listSize;
    }
    let extra = size % 3;
    if (extra > 0) {
      for (let i = 0; i < 3; i++) {
        sizeList[i] += 1;
        extra -= 1;
        if (extra < 1) {
          break;
        }
      }
    }
    let temp1 = [],
      temp2 = [],
      temp3 = [],
      x = 0;
    // creating 3 lists for featurs
    for (let i = 0; i < 3; i++) {
      for (let j = x; j < x + sizeList[i]; j++) {
        if (i == 0) {
          temp1.push(l[j]);
        } else if (i == 1) {
          temp2.push(l[j]);
        } else if (i == 2) {
          temp3.push(l[j]);
        }
      }
      x += sizeList[i];
    }
    console.log(sizeList);
    this.state.featuresListOne = temp1;
    this.state.featuresListTwo = temp2;
    this.state.featuresListThree = temp3;
    // console.log(features);
    this.setState(this.state);
    console.log(this.state);
  };
  getConditionDetails = () => {
    let { allDetails } = this.state;
    // console.log(allDetails);
    let obj = {
      Body: allDetails["dlDescription"] || "NA",
      Mobility: allDetails["dsDescription"] || "NA",
      Motor: allDetails["msDescription"] || "NA",
    };
    this.state.conditionDetails = obj;
    this.setState(this.state);
    console.log(obj);
  };

  getEngineDetails = () => {
    let { allDetails } = this.state;
    console.log(allDetails);
    let obj1 = {
      Carburant: allDetails["carbDescription"] || "NA",
      "co2 emissions": allDetails["euro"] || "NA",
      Volume: allDetails["cc"] + " cm" || "NA",
    };
    let obj2 = {
      Power: allDetails["kw"] + " kW" || "NA",
      Horsepower: allDetails["pk"] + " hp" || "NA",
      Transmission:
        allDetails["gearbox"] === "A"
          ? "Automatic"
          : "Gearbox(" + allDetails["gearbox"] + ")" || "NA",
    };
    this.state.engineDetails1 = obj1;
    this.state.engineDetails2 = obj2;
    this.setState(this.state);
    // console.log(obj);
  };

  handlePopUp = (image_num) => {
    this.state.closePopUp = this.state.closePopUp ? false : true;
    window.scrollTo(0, 0);

    document.body.style.overflow = "hidden";
    this.setState(this.state);
    this.handlePopUpScroll(image_num);
  };

  handlePopUpScroll = (image_num) => {
    console.log(image_num);
    setTimeout(() => {
      console.log(this.popUpImageSection);
      console.log(this.popUpImageSection.scrollHeight);
      console.log(this.popUpImageSection.clientHeight);
      let number_of_cars =
        this.popUpImageSection.scrollHeight /
        this.popUpImageSection.clientHeight;
      let val = this.popUpImageSection.clientHeight * image_num;
      if (window.innerWidth < 769) {
        val /= 2;
      }

      this.popUpImageSection.scroll({
        top: val,
        left: 0,
        behavior: "smooth",
      });
      console.log(number_of_cars);
    }, 10);
  };

  handleDetailType = (type) => {
    console.log(type);
    this.state.detailType = type;
    this.setState(this.state);
    console.log(this.state);
  };

  closePopUp = (e) => {
    console.log(e);
    e.stopPropagation();
    this.state.closePopUp = this.state.closePopUp ? false : true;

    document.body.style.overflow = "";
    this.setState(this.state);
    console.log(this.state);
  };
  handleScrollIndicator = (image) => {
    console.log("image " + image);
    console.log("state " + this.state.image_number);
    // let client = this.scrollIndicator.clientHeight;
    // when car scrolls up
    // let translate
    let diff = Math.abs(image - this.state.image_number);
    if (image < this.state.image_number) {
      this.state.translate += diff * 100;
    } else if (image > this.state.image_number) {
      this.state.translate -= diff * 100;
    }
    this.state.image_number = image;
    this.setState(this.state);
  };
  handleScrollImageOnClick = (image_num) => {
    console.log(image_num);
    console.log(this.carLargeImagesContainer.clientHeight);
    let largeImagesContainerHeight = this.carLargeImagesContainer.clientHeight;
    let scrollCardRef = this.state.scrollCardRef;
    let scrollVal = image_num * scrollCardRef.clientHeight;
    scrollCardRef.scroll(0, scrollVal);
  };

  getScrollCardRef = (scrollCardRef) => {
    this.state.scrollCardRef = scrollCardRef;
    this.setState(this.state);
  };

  render() {
    return (
      <div className="car-detail-container">
        <div className="car-image-scroller">
          <div
            ref={(el) => {
              this.carLargeImagesContainer = el;
            }}
            className="scroll-card-container"
          >
            <ScrollCards
              getScrollCardRef={this.getScrollCardRef}
              popUp={this.handlePopUp}
              carDetails={this.state.pictures}
              scrollIndicator={this.handleScrollIndicator}
            ></ScrollCards>
          </div>
          <div
            ref={(el) => {
              this.scrollIndicator = el;
            }}
            className="car-image-indicator"
          >
            <ul
              style={{
                transform: `translateX(${this.state.translate}px)`,
              }}
            >
              {this.state.pictures.map((image) => (
                <li
                  onClick={() =>
                    this.handleScrollImageOnClick(image.image_number)
                  }
                  key={image.image_number}
                  id={
                    this.state.image_number === image.image_number
                      ? "active"
                      : ""
                  }
                  className="car-image-indicator-li"
                  style={{ backgroundImage: `url(${image.url})` }}
                ></li>
              ))}
            </ul>
          </div>
        </div>
        <div className="car-general-detail-wrapper">
          <div className="car-general-details-container">
            <div className="car-basic-info-container">
              <div className="car-basic-info">
                <div className="car-name">
                  {this.state.carDetails.name}
                  <br></br>
                  <span>{" " + this.state.carDetails.type}</span>
                </div>
                <div className="car-price">
                  &euro; {this.state.carDetails.price}/
                  <span className="car-tax">{this.state.carDetails.tax}</span>
                </div>
              </div>
            </div>
            <div className="car-general-details-box">
              <div className="car-general-details-heading">General Details</div>
              <ul className="car-first-list">
                <li>
                  <div className="car-date-box">
                    <div className="car-date-image"></div>
                    <div className="car-date-info">
                      {this.state.carDetails.date}
                    </div>
                  </div>
                </li>
                <li>
                  <div className="car-date-km">
                    <div className="car-km-image"></div>
                    <div className="car-km-info">
                      {this.state.carDetails.km + " km"}
                    </div>
                  </div>
                </li>
                <li>
                  <div className="car-petrol-box">
                    <div className="car-petrol-image"></div>
                    <div className="car-petrol-info">
                      {this.state.carDetails.feul}
                    </div>
                  </div>
                </li>
                <li>
                  <div className="car-gear-box">
                    <div className="car-gear-image"></div>
                    <div className="car-gear-info">
                      {this.state.carDetails.gearbox}
                    </div>
                  </div>
                </li>
              </ul>
            </div>
            <div className="car-technical-details-container">
              <div className="car-technical-details-box">
                <div className="car-technical-details-heading">
                  Technical Details
                </div>

                <div className="engine-container">
                  <div className="engine-heading">Engine</div>
                  <div className="engine-info">
                    <ul className="engine-first-list">
                      {Object.keys(this.state.engineDetails1).map((data) => (
                        <li key={data}>
                          {" "}
                          <div className="key">{data}</div>
                          <div className="value">
                            {this.state.engineDetails1[data]}
                          </div>
                        </li>
                      ))}
                    </ul>
                    <ul className="engine-second-list">
                      {Object.keys(this.state.engineDetails2).map((data) => (
                        <li key={this.state.engineDetails2[data]}>
                          {" "}
                          <div className="key">{data}</div>
                          <div className="value">
                            {this.state.engineDetails2[data]}
                          </div>
                        </li>
                      ))}
                    </ul>
                  </div>
                </div>
                <div className="condition-container">
                  <div className="condition-heading">Car Condition</div>
                  <div className="condition-info">
                    <ul className="condition-first-list">
                      {Object.keys(this.state.conditionDetails).map((data) => (
                        <li key={data}>
                          {" "}
                          <div className="key">{data}</div>
                          <div className="value">
                            {this.state.conditionDetails[data]}
                          </div>
                        </li>
                      ))}
                    </ul>
                  </div>
                </div>
                <div className="features-container">
                  <div className="features-heading">Other Features</div>
                  <div className="features-info">
                    <ul className="feature-list">
                      {this.state.featuresListOne.map((feature) => (
                        <li key={feature}>{feature}</li>
                      ))}
                    </ul>
                    <ul className="feature-list">
                      {this.state.featuresListTwo.map((feature) => (
                        <li key={feature}>{feature}</li>
                      ))}
                    </ul>
                    <ul className="feature-list">
                      {this.state.featuresListThree.map((feature) => (
                        <li key={feature}>{feature}</li>
                      ))}
                    </ul>
                  </div>
                </div>

                {/* <ul className="detail-list">
                <li
                  className={`engine ${
                    this.state.detailType === "Engine" ? "detail-active" : ""
                  }`}
                  onClick={() => {
                    this.handleDetailType("Engine");
                  }}
                >
                  Engine
                </li>
                <li
                  className={`condition ${
                    this.state.detailType === "Condition" ? "detail-active" : ""
                  }`}
                  onClick={() => {
                    this.handleDetailType("Condition");
                  }}
                >
                  Car Condition
                </li>
                <li
                  className={`feature ${
                    this.state.detailType === "Features" ? "detail-active" : ""
                  }`}
                  onClick={() => {
                    this.handleDetailType("Features");
                  }}
                >
                  Features
                </li>
              </ul>
              <TechnicalDetails
                carDetails={this.state.allDetails}
                details={this.state.detailType}
              ></TechnicalDetails> */}
              </div>
            </div>
          </div>
        </div>

        <div
          onClick={this.closePopUp}
          className={`car-image-pop-up ${
            this.state.closePopUp ? "close-image-pop-up" : ""
          }`}
          ref={(el) => {
            this.popUpImageSection = el;
          }}
        >
          <div onClick={this.closePopUp} className="cross-car-pop-up"></div>
          {this.state.pictures.map((picture, index) => (
            <div
              key={index}
              style={{ backgroundImage: `url(${picture.url})` }}
              className="pop-up-image"
            ></div>
          ))}
        </div>
        <Contact></Contact>
      </div>
    );
  }
}

export default CarDetails;
