import React, { Component } from "react";
import "./home.css";
import video from "../images/people_video.mp4";

import axios from "axios";
import Card from "./car-cards";
import { Link } from "react-router-dom";

import Contact from "./contact";

class Home extends Component {
  state = {
    carDetails: [],
    loading: true,
    rotate: 0,
    opacity: 0,
  };

  componentDidMount() {
    window.scrollTo(0, 0);

    axios
      .get("https://wallyn-messenger.herokuapp.com")
      .then((res) => {
        let result = res.data.result;
        let carList = [];

        let temp = [];
        for (let x in result) {
          temp.push(x);

          console.log(carList);
          console.log(result[x]);
          let pics = result[x]["pictures"];
          carList.push({
            type: result[x]["type"],
            name: result[x]["make"] + " " + result[x]["model"],

            price: result[x]["price"],
            km: result[x]["km"],
            id: x,
            key: "home" + x,
            image: pics[Object.keys(pics)[0]],
            year: result[x]["year"],
            feul: result[x]["carbDescription"],
            tax: result[x]["vat"] > 0 ? "+VAT" : "MARGE",
          });
          console.log("car list");
          carList = this.shuffle(carList);
          console.log(carList);
        }
        console.log(temp);
        this.state.carDetails = carList;
        setTimeout(() => {
          this.state.loading = false;
          this.setState(this.state);
        }, 800);
        console.log(this.state);
        this.setState(this.state);
      })
      .catch((err) => {
        console.log(err);
      });
  }
  compare_price(a, b) {
    // a should come before b in the sorted order
    if (a["price"] < b["price"]) {
      return -1;
      // a should come after b in the sorted order
    } else if (a["price"] > b["price"]) {
      return 1;
      // a and b are the same
    } else {
      return 0;
    }
  }
  compare_km(a, b) {
    // a should come before b in the sorted order
    if (a["km"] < b["km"]) {
      return -1;
      // a should come after b in the sorted order
    } else if (a["km"] > b["km"]) {
      return 1;
      // a and b are the same
    } else {
      return 0;
    }
  }
  compare_year(a, b) {
    // a should come before b in the sorted order
    if (a["year"] < b["year"]) {
      return -1;
      // a should come after b in the sorted order
    } else if (a["year"] > b["year"]) {
      return 1;
      // a and b are the same
    } else {
      return 0;
    }
  }
  compare_make(a, b) {
    // a should come before b in the sorted order
    if (a["name"] < b["name"]) {
      return -1;
      // a should come after b in the sorted order
    } else if (a["name"] > b["name"]) {
      return 1;
      // a and b are the same
    } else {
      return 0;
    }
  }

  handleFilter = (type) => {
    console.log(type);
    if (type === "price") {
      this.state.carDetails.sort(this.compare_price);
      this.setState(this.state);
    } else if (type === "km") {
      this.state.carDetails.sort(this.compare_km);
      this.setState(this.state);
    } else if (type === "year") {
      this.state.carDetails.sort(this.compare_year);
      this.setState(this.state);
    } else if (type === "make") {
      this.state.carDetails.sort(this.compare_make);
      this.setState(this.state);
    } else {
      return;
    }
  };
  handleDropDown = (type) => {
    if (type != "") {
      this.handleFilter(type);
    }

    this.state.rotate = this.state.rotate === 0 ? 180 : 0;
    this.state.opacity = this.state.opacity === 0 ? 1 : 0;
    this.setState(this.state);
  };
  shuffle = (array) => {
    var currentIndex = array.length,
      randomIndex;

    // While there remain elements to shuffle...
    while (0 !== currentIndex) {
      // Pick a remaining element...
      randomIndex = Math.floor(Math.random() * currentIndex);
      currentIndex--;

      // And swap it with the current element.
      [array[currentIndex], array[randomIndex]] = [
        array[randomIndex],
        array[currentIndex],
      ];
    }

    return array;
  };

  // $car["year"]." ".$car["make"]." ".$car["model"]." ".$car["type"];
  render() {
    return (
      <div className="home-container">
        <div className="video">
          <div className="layer">
            <div className="layer-text">
              {/* <h1 className="home-heading">Wallyn Auto Handle</h1>
              <h1 className="home-sub-heading">Since 1966</h1> */}
              <div className="home-logo"></div>
              {/* <h3 className="home-sub-heading">
                Damaged vehicles ! call us for the best prices
              </h3> */}
            </div>

            {/* <div className="call-button">
              <Link to="/contact">Contact Us</Link>
            </div> */}
          </div>
          <video
            poster="../images/poster.png"
            className="video-bg"
            autoPlay
            loop
            muted
          >
            <source src={video} type="video/mp4"></source>
            your browser does not support
          </video>
        </div>
        <div className="filter-bar-container">
          <div className="filter-bar" onClick={() => this.handleDropDown("")}>
            <div
              style={{ transform: `rotate(${this.state.rotate}deg)` }}
              className="filter-image-arrow"
            ></div>
            <div className="filter-image"></div>
          </div>
          <div className="drop-down">
            <ul
              style={{ opacity: this.state.opacity }}
              className="drop-down-list"
            >
              <li onClick={() => this.handleDropDown("price")}>
                Sort by price
              </li>
              <li onClick={() => this.handleDropDown("km")}>Sort by km</li>
              <li onClick={() => this.handleDropDown("year")}>Sort by year</li>
              <li onClick={() => this.handleDropDown("make")}>Sort by make</li>
            </ul>
          </div>
        </div>
        <div className="cars-heading">Our Cars</div>

        {this.state.loading === "" || this.state.loading === true ? (
          <div className="skeleton-cards-container">
            {[1, 2, 3, 4, 5, 6].map((i) => (
              <div key={i} className="skeleton-card">
                <div className="skeleton-image"></div>
                <div className="skeleton-title"></div>
                <div className="skeleton-info"></div>
              </div>
            ))}
          </div>
        ) : (
          <Card carDetails={this.state.carDetails}></Card>
        )}

        <Contact></Contact>
      </div>
    );
  }
}

export default Home;
