import React, { Component } from "react";
import "./car-cards.css";
import { Link } from "react-router-dom";
class Card extends Component {
  state = {
    gridStyle: "",
  };
  componentDidMount = () => {
    console.log(window.innerWidth);
    if (window.innerWidth < 1111 && window.innerWidth >= 768) {
      this.state.gridStyle = "grid-two";
      this.setState(this.state);
    } else if (window.innerWidth < 768) {
      this.state.gridStyle = "grid-one";
      this.setState(this.state);
    } else {
      this.state.gridStyle = "";
      this.setState(this.state);
    }

    window.addEventListener("resize", this.handleResize);
  };

  componentWillUnmount = () => {
    window.removeEventListener("resize", this.handleResize);
  };

  handleResize = () => {
    if (window.innerWidth < 1111 && window.innerWidth >= 768) {
      this.state.gridStyle = "grid-two";
      this.setState(this.state);
    } else if (window.innerWidth < 768) {
      this.state.gridStyle = "grid-one";
      this.setState(this.state);
    } else {
      this.state.gridStyle = "";
      this.setState(this.state);
    }
  };
  render() {
    const { carDetails } = this.props;
    return (
      <div id={this.state.gridStyle} className="car-cards-container">
        {carDetails.map((car, i) => (
          <Link key={i} to={"/carDetails?id=" + car.id}>
            <div className="car-cards">
              <div
                style={{ backgroundImage: `url(${car.image})` }}
                className="card-image"
              >
                <div className="money">&euro; {car.price}</div>
              </div>
              <div className="card-text">
                {car.name + " "}
                <br></br>
                <span>{" " + car.type}</span>
              </div>

              <ul>
                <li>
                  {" "}
                  <div className="car-petrol-image"></div>
                  {car.feul}
                </li>
                <li className="mid-list">
                  <div className="car-km-image"></div>
                  {car.km} km
                </li>
                <li>
                  <div className="car-date-image"></div>
                  {car.year}
                </li>
              </ul>
            </div>
          </Link>
        ))}
      </div>
    );
  }
}

export default Card;
